/* Header */
header {
  background-color: #FFD700;
  padding: 20px;
  text-align: center;
  margin-bottom: 50px;
}

header h1 {
  font-size: 40px;
  font-weight: bold;
  color: #FFFFFF;
  text-transform: uppercase;
  letter-spacing: 2px;
}

header p {
  font-size: 16px;
  color: #FFFFFF;
  margin-top: 10px;
}

header a {
  color: #FFFFFF;
  text-decoration: none;
}

header a:hover {
  text-decoration: underline;
}

.Home_logo__2dbV8 {
  width: 100px;
  height: 100px;
}


h1 {
  font-size: 24px;
  font-weight: bold;
}


.Home_grid__3MgUw {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr; 
  grid-gap: 20px;
}

.Home_card__ii6rI {
  display: flex;
  align-items: center;
  background-color: #ffffff;
  border-radius: 10px;
  padding: 10px;
  width: 600px;
  height: 250px;
  margin: 0 auto;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.Home_img__1l5uw {
  max-height: 100%;
  max-width: 300px; 
  border-radius: 10px;
}

.Home_imgContent__1tIjv {
  padding-right: 20px;
}

.Home_content__2DapC {
  display: flex;
  flex-direction: column;
  justify-content: start;
}

h2 {
  font-size: 24px;
  margin-bottom: 5px;
}

span {
  font-size: 16px;
  margin-bottom: 10px;
}

.Home_publish__63Ge8 {
  font-size: 12px;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

